import React from "react";
import getWordFrequency from "../components/WordFrequency";

function TextFrequency() {
  return (
    <table>
      <tr>
        <th></th>
        <th></th>
      </tr>
      {getWordFrequency().map((item) => (
        <tr>
          <td>{item.word}</td>
          <td>{item.count}</td>
        </tr>
      ))}
    </table>
  );
}

export default TextFrequency;
